
.mv-skeleton-wrap {
  width: 100%;
  // 骨架屏
  .mv-loading-wrap {
    width: 100%;
    // 默认骨架屏
    .mv-default-wrap{
      width: 100%;
      box-sizing: border-box;
      padding: 0.16rem;
      overflow: hidden;
      // 水平方向骨架
      .vm-row-wrap{
        .mv-skeleton-title {
          width: 40%;
          height: 0.32rem;
          background-color: #f2f3f5;
        }
        .mv-skeleton-paragraph {
          width: 100%;
          height: 0.32rem;
          box-sizing: border-box;
          background-color: #f2f3f5;
          margin-top: 0.16rem;
        }
      }
      // 垂直方向骨架
      .vm-column-wrap{
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        .skeleton-bar{
          width: 0.56rem;
          height: 100%;
          box-sizing: border-box;
          background-color: #f2f3f5;
        }
      }
    }
  }
  // 加载完成业务区域
  .mv-completed-wrap {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
}
